@import '../shared/helper';

.home {
  flex: 1;
  height: 100%;
  display: flex;
  flex-direction: column;
  background-image: '../assets/img/bg01.png';
  > header {
    width: px(1920);
    margin: 0 auto;
    height: px(80);
    background-size: cover;
    text-align: center;
    font-size: px(50);
    // color: #5dc2fe;
    color: #9fd5f5;
    padding: px(18);
    // text-shadow: 0 0 px(30) #69baf8;
    font-weight: bold;
    animation: shining 0.4s alternate infinite;
  }
  > footer {
    height: px(80);
    margin-top: px(1) 0 1px;
    border: px(3) solid #0d2d59;
    border-radius: 4px;
    background: #062c42f1;
    font-size: px(16);
    color: rgb(178, 204, 243);
    display: flex;
    align-items: center;
    justify-content: center;
  }
  > main {
    z-index: 1;
    width: px(1920);
    height: px(920);
    margin: 0 auto; //main元素居中
    padding: px(20);
    flex: 1;
    display: grid; //grid布局
    grid-template:
      'box1 box2 box3 box4' 300fr
      'box5 box6 box6 box7' 290fr
      'box5 box6 box6 box8' 290fr / 500fr 490fr 490fr 400fr;
    grid-column-gap: px(18);
    grid-row-gap: px(18);

    .bordered {
      border: 2px solid #6bc5fa;
      border-radius: 10px;
      position: relative;
      box-shadow: 0 0 2px 0 #0e325f, inset 0 0 2px 0 #0e325f;
      background: #05253856;
      &::before {
        pointer-events: none;//不响应鼠标点击事件
        content: '';
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        top: 0;
        border-radius: 10px;
        box-shadow: 20px 0 0 -18px #133661, -20px 0 0 -18px #0e325f,
          0 20px 0 -18px #0e325f, 0 -20px 0 -18px #0e325f, 10px 0 0 -8px #164b88,
          -10px 0 0 -8px #0d4483, 0 10px 0 -8px #0d4483, 0 -10px 0 -8px #0d4483;
      }
      h2 {
        width:px(300);
        margin:0 auto;
        background: #19659162;
        color: #85d0fc;
        flex-shrink: 0;
        border: 1px solid #0a5299;
        border-radius: 0 0 px(20) px(20);
        border-bottom-left-radius: 4px;
        border-bottom-right-radius: 4px;
        font-size: px(22);
        line-height: px(24);
        padding: px(10) px(28);
        text-shadow: 0 0 px(2) rgb(218, 210, 210);
        img {
          width: px(22);
          height: px(22);
          vertical-align: -0.15em;
          fill: currentColor;
          overflow: hidden;
        }
      }
    }
    > section {
      height: 100%;
      text-align: center;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }
    > .section1 {
      grid-area: box1;
    }
    > .section2 {
      grid-area: box2;
    }
    > .section3 {
      grid-area: box3;
    }
    > .section4 {
      grid-area: box4;
    }
    > .section5 {
      grid-area: box5;
    }
    > .section6 {
      grid-area: box6;
      display: flex;
      flex-direction: column;
    }
    > .section7 {
      grid-area: box7;
    }
    > .section8 {
      grid-area: box8;
    }
  }
}
