@import '../../shared/helper';

.benefit {
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  height: px(290);
  h2 {
    background: #19659162;
    color: #85d0fc;
    flex-shrink: 0;
    border: 1px solid #0a5299;
    border-radius: 0 0 px(20) px(20);
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
    font-size: px(22);
    line-height: px(24);
    padding: px(10) px(28);
    text-shadow: 0 0 px(2) rgb(218, 210, 210);
    img {
      width: px(30);
      height: px(30);
      vertical-align: -0.15em;
      fill: currentColor;
      overflow: hidden;
    }
  }
  .chart {
    flex: 1;
    width: 100%;
  }
}
